import React from "react"

import image1 from "../images/projects/veda/login.png"
import image2 from "../images/projects/veda/tables.png"
import image3 from "../images/projects/veda/modals.png"
import image4 from "../images/projects/veda/alerts.png"

import { FaArrowCircleLeft, FaArrowCircleRight } from "react-icons/fa"
import { Fade } from "react-slideshow-image"
import "react-slideshow-image/dist/styles.css"
import SEO from "../components/seo"
import Layout from "../components/layout"

const fadeImages = [image1, image2, image3, image4]

const properties = {
  prevArrow: (
    <FaArrowCircleLeft
      style={{ width: 50, height: 90, marginRight: 30, cursor: "pointer" }}
    />
  ),
  nextArrow: (
    <FaArrowCircleRight
      style={{ width: 50, height: 90, marginLeft: 30, cursor: "pointer" }}
    />
  ),
}

const veda = () => {
  return (
    <Layout>
      <div className="slide-container">
        <SEO title="Projects" />
        <Fade {...properties}>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[0]} alt="sc 1" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[1]} alt="sc 2" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[2]} alt="sc 3" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[3]} alt="sc 4" />
            </div>
          </div>
        </Fade>

        <div
          style={{
            fontSize: 25,
            marginBottom: 20,
            color: "#c8d5c3",
          }}
        >
          Veda
        </div>
        <div>
          An upgraded version of the the previous operations system which uses
          ReactJs for better performance. The application is still under
          development and will also come with new features and optimizations
          like realtime data, alerts etc.
        </div>
      </div>
    </Layout>
  )
}

export default veda
